@import url(https://fonts.googleapis.com/css2?family=Karla:wght@200;300;400;500;600;700;800&family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
===============================
Variables
==============================-
*/
:root {
  --color1: #C38D9E;
  --color2: #E8A87C;
  --color3: #009177;
  --color4: #184860;
}

/*
===================================================================================================
Body/utils
===================================================================================================
*/

html{
  scroll-behavior: smooth;
}
:root{
  font-size: calc(12px + (34 - 14) * ((100vw - 1000px) / (1700 - 300)));
}

body{
  background-color:#fffefe  !important;
  line-height: 1.64;
  
}

.arrow {
  border: solid black;
  border-width: 0 .14em .14em 0 ;
  display: inline-block;
  padding: .4em;

}

.down {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.up {
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
}

.display-3{
  font-size: 4.2em !important;
}

.mt-c{
  margin-top: 10rem;
}

.mobile{
  display:none;
}


/*
===================================================================================================
Nav Stuff
===================================================================================================
*/
.side-nav{
  position:fixed  !important;
  top: 0;
  left: 0;  
  z-index: 999;
}

.nav>a{
  color: #001c00;
  font-family: Roboto;
  font-weight: normal;
  padding: .5rem .4rem !important;

  border-right: .25em solid transparent;
  transition: .3s;
  
  

}
.nav>a>strong{
  font-weight: bolder;

}
.nav-item.active,.nav>a:hover{

  border-right: .25em solid rgb(224, 224, 224);
  

}

.nav>a:hover.color1,.active.nav-item.color1{
  border-color: #C38D9E;
  border-color: var(--color1);

}
.nav>a:hover.color2,.active.nav-item.color2{
  border-color: #E8A87C;
  border-color: var(--color2);

}
.nav>a:hover.color3,.active.nav-item.color3{
  border-color: #009177;
  border-color: var(--color3);

}
.nav>a:hover.color4,.active.nav-item.color4{
  border-color: #184860;
  border-color: var(--color4);

}

.side{
  /* background-color:rgb(248, 193, 193); */
  min-height: 100vh;
  overflow: auto;

}


.centre{

  
  overflow-y: hidden;
  overflow-x: hidden  ;

}





/*
===================================================================================================
Hero
===================================================================================================
*/

.hero-img{
  overflow: auto;
  height: 100vh;
}

.hero-img >img{

  width: 100%;
  height: 100%;
  object-fit: cover;
  
}

.icon>a>img{
  height: 1.5em;
  /* width : 1em; */
  padding-right: .5em;
  margin-bottom: .2em;
}

a, a:hover, a:focus, a:active {
  text-decoration: none !important;
  color: black !important;
}
/*
===================================================================================================
Section Blocks
===================================================================================================
*/

.section{
  min-height: 100vh;
}
.section-title{
  color:white;
  
}

.content{
  background-color: #f8f3f7;
  
}
.section-content{
  color:#55555;

 
}

.about{

  color:#605777;
}


/*
===================================================================================================
accordion
===================================================================================================
*/
.acc-continer{
  width:100%;

}

.fold{
  width:100%;
  border-bottom:#052123 .19em solid; 
   

}



.fold-head{

  font-family: Karla;
  font-weight: 600;
  padding: 2em 1em;
  transition: padding-bottom 0.6s ease;
  color: #333333;
  
  
}
.fold-head>h5{
  font-size: 1.5em
}
.fold-head:hover,.active>.fold-head{

  padding-bottom: 2.5em;
  
}

.acc-icon{
  transition: -webkit-transform 0.6s ease;
  transition: transform 0.6s ease;
  transition: transform 0.6s ease, -webkit-transform 0.6s ease;
}


.fold-content{
  font-family: Karla;
  font-weight: 300;
  overflow: hidden;
  transition: max-height 0.6s ease; 
  color: #555555;

  
}

.skill-list{
  margin: 0;
  padding-left: 1.2rem;
  line-height: 1.25em;

  
}
.skill-list li{
  position: relative;
  list-style-type: none;
  font-weight: normal;
  text-align: left;
  padding-left: 1.1rem;
  margin-bottom: 0.5rem;
}

.skill-list li:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: -2.5px;
  width: 5px;
  height: 11px;
  border-width: 0 .15em 0 0;
  border-style: solid;
  border-color: #605777;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
/*
===================================================================================================
experience
===================================================================================================
*/

.experience{

  
  font-family: Roboto;
  


}

.experience .col-4 >p{
  letter-spacing: .1em;
  
}


.experience .col-4 > strong{
  
  font-size: 1.0em;
  display: block;
  font-weight: 600;

}
.experience .col-8 > strong{
  
  font-size: 1.0em;
  display: block;
  font-weight: 500;


  
  
}
.experience .col-8 > ul {
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;

}

.experience > div > p {
  font-weight: 300;
  font-size: .81em;

  

}



.about > h5{
  font-family: Karla;
  font-size: 1.2em;
  font-weight: 700;
  line-height: 135%;


}
.about > p{
  font-family: Roboto;
  font-weight: 300;
  line-height: 180%;


  
}



.color{
  background-color: white;
}

.about-card >li{
  padding-bottom: .6em;
}

.about-card >li>strong{
  font-size: 1.1em;
}

.about-card >li>span{
  padding-left: .4em;
}



.temp {

  background-color: #009177;

  background-color: var(--color3);
  height: 45%;
  width : 100%;
  

}

/*
===================================================================================================
Portfolio
===================================================================================================
*/

.project-container {

  background-color: #fffefe;
  
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: .3s;
  margin-bottom: 2.0em;
}
.card-container{
  height: 100vh;
  overflow-y: scroll;
  scrollbar-width:thin;

}
.card-container::-webkit-scrollbar {
  width: 12px;               /* width of the entire scrollbar */
}
.project-container p{

  font-family: karla;
  font-size: .9em;
}
.project-thumbnail{
  height: 15rem;

  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
  background-size: 100%;
  -webkit-align-items: center;
          align-items: center;
  color : white;

}


.project-container img{

  min-height: 100%;
  min-width: 100%;
  /* IE 8 */
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  /* IE 5-7 */
  filter: alpha(opacity=0);
  /* modern browsers */
  opacity: 0;
}

.tag{
  border: 2px #3a3b3a solid;  
  border-radius: .2em;
  padding: .1em .5em;
  margin-bottom: .5em;

}

.project-tags{
 
  margin:  1.5em auto;
}

.card-specs{

  line-height: .6em;
  margin:  2em 0;
  font-size: .9em;
}
.project-links{
 
  margin-bottom:1em ;
  
  
  
}
.project-links>li{
  padding-right: .5em;
  
}
.project-links a{
  border-bottom: 1.3px #001c00 dotted;
  font-size: .8em;
}

.project-container.collapsed{
  height: 15rem;
}
.project-container.expanded{
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
}

.hidden-content{
 
}

.collapsed .hidden-content{
  display:none;


}

.expanded .hidden-content{
  display: inherit;


}

.toggle-container{
  color:#737373  !important; 
  border-bottom: 1.3px #737373  dotted;
  margin-bottom: 1em;

}

.toggle-container:active,.toggle-containe:hover{
  color:#001c00  !important; 
  border-bottom: 1.3px #000000  dotted;
  
  
} 

/*
===================================================================================================
Mobile
===================================================================================================
*/


@media only screen and (max-width: 500px) {

  body {
    

    background-color: white  !important;
    height: 100vh;
  }
  .large-screen{
    
    display: none;
  }
  .mobile{
    
    display: block;
    text-align: center;
  }

  strong{
    display: block;
    padding: 1em .25em;
    margin: 1em;
    border: .2em solid black;
    transition: padding .2s linear;
  }

  strong:hover{
   padding: 1.5em .25em;
  }
 

}

